<template>
  <div class="skladnik">
    <div class="leska">
      <img :src="url" :alt="nazwa" class="img" />
      {{ nazwa }}
    </div>
    <div class="option-bar">
      <div class="minus" @click="minusKlikniety(id)" :class="{ dis: minusDis }">
        -
      </div>
      <div class="val">{{ count }}</div>
      <div class="plus" @click="plusKlikniety(id)" :class="{ dis: plusDis }">
        +
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['id', 'nazwa', 'count', 'url'],
  data() {
    return {
      minusDis: false,
      plusDis: false
    };
  },
  methods: {
    minusKlikniety(id) {
      console.log(this.$store);
      this.$store.dispatch('remove', { id: id });
    },
    plusKlikniety(id) {
      this.$store.dispatch('add', { id: id });
    }
  },
  watch: {
    count(state) {
      if (state === 3) this.plusDis = true;
      else this.plusDis = false;
      if (state === 0) this.minusDis = true;
      else this.minusDis = false;
    }
  }
};
</script>

<style scoped>
.img {
  width: 100%;
  pointer-events: none;
  user-select: none;
}
.skladnik {
  width: 100px;
}
.leska,
.option-bar {
  display: flex;
  align-items: center;
  align-items: center;
  color: black;
  width: 100%;
}
.leska {
  justify-content: center;
  flex-direction: column;
  padding: 20px;
  padding-bottom: 3px;
  background-color: white;
  border-radius: 30px;
  border-top-left-radius: 0;
}
.option-bar {
  justify-content: space-between;
  height: 25px;
  background-color: #ffe49d;
  margin-top: 20px;
  user-select: none;
  border-radius: 10px;
}
.minus,
.plus {
  width: 35px;
  height: 35px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  user-select: none;
  border-radius: 3px;
}
.minus {
  background-color: #9e3214;
}
.plus {
  background-color: #9e3214;
}
.plus:hover {
  background-color: #2edd40;
}
.minus:hover {
  background-color: #ff3535;
}
.dis {
  background-color: rgb(51, 51, 51);
  pointer-events: none;
}
@media (min-width: 1200px) {
  .skladnik {
    width: 150px;
  }
  .minus,
  .plus {
    width: 35px;
    height: 35px;
  }
}
</style>
