<template>
  <div class="wygContainer">
    <BaseTitle>
      {{ nazwaPizzy }}
    </BaseTitle>
    <div class="imgsContainer" ref="printMe">
      <transition-group name="skl" mode="out-in">
        <div
          class="sklIMG"
          v-for="dodany in dodane"
          :key="dodany.id"
          :style="{ zIndex: dodany.zindex + 1000 }"
        >
          <img
            :src="ingURL(dodany.nazwa, dodany.count)"
            :alt="dodany.nazwa"
            class="spodimgs"
          />
        </div>
      </transition-group>
      <transition name="spod" mode="out-in">
        <img src="../assets/img/skladniki/spod.png" alt="spod" class="spod" />
      </transition>
    </div>
    <h3>Składniki</h3>
    <div class="skl">{{ skladniki }}</div>
    <form
      v-if="!wpisana"
      @submit.prevent="
        print();
        schowajInput();
      "
    >
      <h3>Nazwa pizzy</h3>
      <input type="text" v-model="nazwaPizzy" maxlength="30" /><input
        type="submit"
        value="Potwierdź"
      />
    </form>
    <a class="dow" v-if="output" :href="output" download>Pobierz</a>
  </div>
</template>

<script>
export default {
  data() {
    return {
      nazwaPizzy: '',
      wpisana: false,
      output: null
    };
  },
  computed: {
    dodane() {
      return this.$store.getters.dodaneSkladniki;
    },
    skladniki() {
      let nazwy = this.dodane.map(el => el.nazwa.toLowerCase());
      return `sos, ser, oregano, ${nazwy.join(', ')}`;
    }
  },
  methods: {
    ingURL(nazwa, ilosc) {
      // let baseURL = '../assets/img/pizzagr/';
      let nazwaS = nazwa.toLowerCase();
      return require('../assets/img/pizzagr/' + nazwaS + ilosc + '.png');
      // return require(`${baseURL}${nazwaS}${ilosc}.png`);
    },
    schowajInput() {
      this.wpisana = true;
    },
    async print() {
      const el = this.$refs.printMe;
      const options = {
        type: 'dataURL'
      };
      this.output = await this.$html2canvas(el, options);
    }
  }
};
</script>

<style scoped>
.dow {
  color: white;
  padding: 5px 10px;
  position: relative;
  top: 30px;
  font-size: 30px;
  text-decoration: none;
  background-color: rgb(128, 36, 0);
  border-radius: 10px;
}
.dow:hover {
  background-color: rgb(163, 53, 9);
  cursor: pointer;
}
.wygContainer {
  margin-top: 50px;
}

input[type='text'] {
  border: 0;
  outline: 0;
  border-radius: 10px;
  padding: 5px;
  margin-right: 10px;
}
input[type='submit'] {
  background-color: orange;
  color: black;
  border: 0;
  outline: 0;
  border-radius: 10px;
  padding: 5px;
}
input[type='submit']:hover {
  background-color: rgb(94, 64, 9);
  color: white;
  cursor: pointer;
}
.imgsContainer {
  position: relative;
}
.skl {
  max-width: 100%;
  color: gray;
}
.spod {
  max-width: 100%;
}
.spodimgs {
  max-width: 100%;
}
.sklIMG {
  max-width: 100%;
  position: absolute;
}
.spod-enter-active {
  animation: slide 0.3s ease-out;
}
.spod-leave-active {
  animation: slide 0.3s reverse;
}
.skl-enter-active {
  animation: slide 0.3s ease-out;
}
.skl-leave-active {
  animation: slide 0.3s reverse;
}
.ski-move {
  transition: all 0.5s;
}
@keyframes slide {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@media (min-width: 1200px) {
  .wygContainer {
    margin-top: 0;
  }

  .skl {
    max-width: 500px;
  }
  .sklIMG {
    max-width: 500px;
  }
}
</style>
